@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Noto Sans', sans-serif;
}


html {
	scroll-behavior: smooth;
}

body::-webkit-scrollbar {
	display: none;
}

#view-product {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.541);
	z-index: 15;
}

header {
	padding: 20px 30px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	background: #f5a23d;
	color: white;
}

.logo {
	font-size: 30px;
	letter-spacing: 2px;
	font-feature-settings: "smcp";
	font-variant: small-caps;
	font-weight: 550;
}

header ul li {
	display: inline-block;
	list-style: none;
	margin-left: 10px;
}

header ul li button {
	border: none;
	text-decoration: none;
	background: rgba(37, 37, 37, 0.452);
	padding: 8px 15px;
	color: white;
	border-radius: 5px;
}

header ul li button:focus {
	outline: none;
}

.content {
	display: -webkit-flex;
	display: flex;
}

.fa-bars {
	display: none;
}

/* Categories */
.categories {
	min-width: 250px;	
	height: 100vh;
	padding: 20px 15px;
	font-family: 'Noto Sans', sans-serif;
}

#toggleCategories {
	display: none;
}

.categories label {
	display: block;
	font-size: 20px;
	text-align: center;
	margin-bottom: 20px;
	letter-spacing: 1px;
	padding-bottom: 5px;
}

.categories label::after {
	content: '';
	display: block;
	height: 3px;
	width: 50px;
	background: #f5a23d;
	margin: 9px auto 0;
}

.categories ul {
	background: white;
}

.products {
	width: 100%;
	box-shadow: 0 1px 1px black;
}

.cart {
	min-width: 300px;
	padding: 20px 0;
	position: relative;
}

#toggleCart {
	display: none;
}

.cart > label {
	display: block;
	font-size: 20px;
	text-align: center;
	margin-bottom: 15px;
	letter-spacing: 1px;
	padding-bottom: 5px;
}

.cart > label::after {
	content: '';
	display: block;
	height: 3px;
	width: 50px;
	background: #f5a23d;
	margin: 5px auto 0;
}

.cart-items-container {
	background: white;
}

.cart-items {
	padding: 0 5px;
}

.process-cart-items {
	position: -webkit-sticky;
	position: sticky;
	bottom: 0;
	background: white;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	box-shadow: 0 0 1px black;
}

.calculations {
	width: 60%;
	padding: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.total-label {
	-webkit-align-self: baseline;
	        align-self: baseline;
	font-size: 10px;
	text-transform: uppercase;
}

.total-amount {
	-webkit-align-self: baseline;
	        align-self: baseline;
	font-size: 18px;
	letter-spacing: 1px;
	margin-left: 20px;
	font-weight: bold;
}

.remove-items, .buy-items {
	width: 20%;
	font-size: 18px;
	color: #fff;
}

.calculations, .remove-items, .buy-items {
	height: 50px;
	border-radius: none;
	border: none;
}

.remove-items {
	background:rgb(255, 106, 106);
}

.buy-items {
	background:rgb(74, 192, 74);
}

.remove-items:focus,
.buy-items:focus {
	outline: none;
}

.remove-items:active,
.buy-items:active {
	opacity: 0.8;
}

.select-all {
	position: -webkit-sticky;
	position: sticky;
	bottom: 50px;
	box-shadow: 0 0 1px black;
	padding: 2px 10px;
	font-size: 14px;
	background: white;
}

.select-all input {
	margin-right: 5px;
}

.select-all label {
	letter-spacing: 1px;
}

.items-selected {
	float: right;
	font-weight: bold;
	font-size: 13px;
}

.go-to-top {
	text-decoration: none;
	background: #f5a23d;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	position: fixed;
	bottom: 20px;
	right: 20px;
	color: white;
	display: none;
	box-shadow: 0 -1px 5px rgba(8, 8, 8, 0.685);
}



@media (max-width: 599px) {

	.content {
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}

	.categories {
		-webkit-order: 1;
		        order: 1;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		padding: 0;
		margin: 5px;
	}

	.categories label {
		margin: 0;
		padding: 20px 0;
		background: #f5a23dc4;
		color: white;
		font-weight: bold;
		border-radius: 5px;
	}

	.categories label::after {
		display: none;
	}

	.cart {
		-webkit-order: 2;
		        order: 2;
		margin: 0 5px 5px;
		padding: 0;
	}

	.cart > label {
		background: #f5a23dc4;
		color: white;
		padding: 20px 0;
		font-weight: bold;
		margin: 0;
	}

	.cart label::after {
		display: none;
	}

	.products {
		-webkit-order: 3;
		        order: 3;
	}

	.categories, .cart {
		width: 100%;
	}

	.categories ul {
		height: 0;
		overflow: hidden;
		transition: all 1s;
		opacity: 0;
		z-index: 15;
	}

	#toggleCategories:checked ~ ul {
		height: 235px;
		opacity: 1;
	}

	.cart-items-container {
		display: none;
	}

	#toggleCart:checked ~ .cart-items-container {
		display: block;
	}

	.go-to-top {
		display: block;
		display: -webkit-flex;
		display: flex;
	}

}


@media (min-width: 600px) and (max-width: 900px) {

	.content {
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}

	.categories, .cart {
		background: white;
		width: 46%;
		position: sticky;
		position: -webkit-sticky;
		top: 5px;
		left: 0;
		z-index: 5;
		margin: 5px 10px;
		border-radius: 10px;
		transition: all .3s;
		-webkit-backdrop-filter: blur(10px);
		        backdrop-filter: blur(10px);
	}

	.categories:hover, .cart:hover {
		/* background: #f5a23d; */
		background: #f5a23dc4;
		color: white;
	}

	.cart .cart-item {
		color: black;
	}

	.total-label, .total-amount {
		color: black;
	}

	.categories {
		-webkit-order: 1;
		        order: 1;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		padding: 20px 0;
	}

	#toggleCategories:checked ~ ul {
		display: block;
	}

	.categories label {
		margin: 0;
		padding: 0;
	}

	.categories label::after {
		display: none;
	}

	.categories ul {
		display: none;
		width: 100%;
		box-shadow: 0 0 5px black;
	}

	#toggleCart:checked ~ .cart-items-container {
		display: none;
	}

	.products {
		-webkit-order: 3;
		        order: 3;
		width: 100%;
	}

	.cart {
		-webkit-order: 2;
		        order: 2;
		min-width: 250px;
	}

	.cart label {
		margin: 0;
		padding: 0;
	}

	.cart label::after {
		display: none;
	}

	.cart-items-container {
		display: block;
		width: 100%;
		background: white;
		max-height: 500px;
		overflow-y: auto;
		padding: 10px 0 0;
		box-shadow: 0 0 5px black;
	}

	.cart-items-container::-webkit-scrollbar {
		width: 7px;
	}

	.cart-items-container::-webkit-scrollbar-thumb {
		background: orange;
		border-radius: 10px;
	}

	.cart-items {
		display: block;
	}

	.categories ul, .cart-items-container {
		position: absolute;
		margin-top: 25px;
	}

	#toggleCart:checked ~ .cart-items {
		display: none;
	}

	.select-all {
		color: black;
	}

	.go-to-top {
		display: block;
		display: -webkit-flex;
		display: flex;
	}

}
.categ-loading-anim {
	height: 70px;
	width: 100%;
	background: #f3f3f3;
	position: relative;
	border-radius: 5px;
}

.categ-loading-anim i {
	position: absolute;
	font-size: 45px;
	top: 50%;
	left: 50%;
	color: #f5a23d;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	-webkit-animation: spin 2s ease-out infinite;
	        animation: spin 2s ease-out infinite;
}


@-webkit-keyframes spin {
	from {
		-webkit-transform: translate(-50%, -50%) rotate(0deg);
		        transform: translate(-50%, -50%) rotate(0deg);
	}
	to {
		-webkit-transform: translate(-50%, -50%) rotate(360deg);
		        transform: translate(-50%, -50%) rotate(360deg);
	}
}


@keyframes spin {
	from {
		-webkit-transform: translate(-50%, -50%) rotate(0deg);
		        transform: translate(-50%, -50%) rotate(0deg);
	}
	to {
		-webkit-transform: translate(-50%, -50%) rotate(360deg);
		        transform: translate(-50%, -50%) rotate(360deg);
	}
}
